<template>
  <div>
    <!--  -->
    <el-form :inline="true" :model='para'>
      <el-form-item>
        <el-input placeholder="" size="small" v-model='para.name' @keyup.native.enter="getLists"></el-input>
      </el-form-item>
      <!-- bug input-->
      <el-form-item class="hide">
        <el-input></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click='getLists'></el-button>
        <el-button type="primary" size="small" @click='Object.assign(para, {name: ""})'></el-button>
      </el-form-item>
    </el-form>
    
    <!--  -->
    <el-button-group>
      <el-button type="info" icon="plus" @click="handleEdit"></el-button>
      <el-button type="danger" icon="delete" @click="handleDel" :disabled="multipleSelection.length < 1">
      </el-button>
    </el-button-group>
    
    <!--  -->
    <el-table
      :data="lists"
      border
      stripe
      tooltip-effect="dark"
      v-loading="para.listLoading"
      @selection-change="handleSelectionChange"
      style="width: 100%">
      <el-table-column type="expand">
        <template scope="scope">
          <p><span class="m-r-sm">:</span>{{ scope.row.errMsg }}</p>
          <el-table :data="scope.row.reqContent"
                    border
                    stripe
                    tooltip-effect="dark"
                    style="width: 100%;"
                    class="m-t-sm m-b-sm">
            <el-table-column prop="name"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="required"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="type"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="maxLength"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="info"
                             label=""
                             align="center"
                             min-width="150">
            </el-table-column>
          </el-table>
          <el-table :data="scope.row.resContent"
                    border
                    stripe
                    tooltip-effect="dark"
                    style="width: 100%;">
            <el-table-column prop="name"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="required"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="type"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="maxLength"
                             label=""
                             align="center"
                             min-width="80">
            </el-table-column>
            <el-table-column prop="info"
                             label=""
                             align="center"
                             min-width="150">
            </el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column
        type="selection"
        min-width="50">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        align="center"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="path"
        label=""
        header-align="center"
        min-width="150">
      </el-table-column>
      <el-table-column
        prop="method"
        label=""
        align="center"
        min-width="100">
      </el-table-column>
      <el-table-column
        prop="errCode"
        label=""
        align="center"
        min-width="80">
      </el-table-column>
      <el-table-column
        prop="show"
        label=""
        align="center"
        min-width="100">
        <template scope="scope">
          <i class="fa" :class="scope.row.show == 1 ? 'fa-check' : 'fa-times'"></i>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        min-width="120">
        <template scope="scope">
          <el-button @click="handleEdit(scope.$index, scope.row)" size="small" icon="edit"></el-button>
          <el-button @click="handleDel(scope.$index, scope.row)" type="danger" size="small" icon="delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <!--  -->
    <el-pagination
      :current-page="para.page"
      :page-size="para.pageSize"
      :total="para.total"
      :layout="$store.state.global.pageLayout"
      @current-change="handleCurrentChange"
      class="text-center m-t-sm">
    </el-pagination>
    
    <!-- / -->
    <el-dialog :title="editData.id ? '' : ''" size="large" v-model="editVisible" top="30px"
               :close-on-click-modal="false">
      <el-form :model="editData" ref="editForm" :label-width="$store.state.global.formLabelWidth"
               :rules="editFormRules">
        <el-row :gutter="10">
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="" prop="name">
              <el-input v-model="editData.name" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="12" :sm="12" :lg="8">
            <el-form-item label="">
              <el-radio-group v-model="editData.show">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="">
              <el-input v-model="editData.path" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="">
              <el-input v-model="editData.errCode" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="12" :sm="12" :lg="8">
            <el-form-item label="">
              <el-select v-model="editData.method" placeholder="">
                <el-option label="get" value="1"></el-option>
                <el-option label="post" value="2"></el-option>
                <el-option label="put" value="3"></el-option>
                <el-option label="delete" value="4"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="12">
            <el-form-item label="">
              <el-input
                placeholder=""
                v-model="editData.errMsg"
                auto-complete="off">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24">
            <el-table :data="editData.reqContent">
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.name"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-radio-group v-model="scope.row.required">
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="0"></el-radio>
                  </el-radio-group>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.type"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.maxLength"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.info"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
          <el-col :xs="24">
            <el-table :data="editData.resContent">
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.name"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-radio-group v-model="scope.row.required">
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="0"></el-radio>
                  </el-radio-group>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.type"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.maxLength"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                align="center"
                min-width="120">
                <template scope="scope">
                  <el-input
                    v-model="scope.row.info"
                    auto-complete="off">
                  </el-input>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary" @click="handleSubmit" :loading="para.editLoading"> </el-button>
      </div>
    </el-dialog>
  
  </div>

</template>

<script>
  import {commonApi} from '@/config'
  export default{
    name: 'api',
    data () {
      return {
        // 
        lists: [],
        
        // ,
        para: {
          name: '',
          page: 1,
          total: 0,
          pageSize: 10,
          editLoading: false,
          listLoading: false
        },
        
        /*  */
        multipleSelection: [],
        
        /* , */
        editVisible: false,
        editData: {
          id: 0,
          name: '',
          path: '',
          method: 'get',
          errCode: '',
          errMsg: '',
          reqContent: [{
            name: '',
            required: 1,
            type: 'String',
            maxLength: 0,
            info: ''
          }],
          resContent: [{
            name: '',
            required: 1,
            type: 'String',
            maxLength: 0,
            info: ''
          }],
          show: 1
        },
        editFormRules: {
          name: {
            required: true,
            message: '',
            trigger: 'blur'
          }
        },
        
      }
    },
    props: [],
    components: {},
    computed: {
    },
    watch: {},
    methods: {
      // 
      getLists(){
        this.para.listLoading = true;
        let para = {
          name: this.para.name,
          page: this.para.page,
          pageSize: this.para.pageSize
        };
        commonApi.getData.call(this, '/common/apiList', para, data => {
          this.lists = data.lists;
          this.para.total = data.total;
          this.para.listLoading = false;
        })
      },
      
      // 
      handleCurrentChange(val) {
        this.para.page = val;
        this.getLists();
      },
      
      // 
      resetQuery(){
        Object.assign(this.para, {name: ''})
      },
      
      //  
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      
      //  
      handleDel(index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          let para = {};
          para.id = row ? row.id : this.multipleSelection.map(item => item.id);
          commonApi.deleteData.call(this, '/common/removeApi', para, () => {
            this.getLists();
            this.$message({
              message: '',
              type: 'success'
            });
          })
        })
      },
      
      //  ,
      handleEdit(index, row){
        this.editVisible = true;
        /* Object.assign(target, ...sources) */
        /*  */
        Object.assign(this.editData, {
          id: 0,
          name: '',
          path: '',
          method: 'get',
          errCode: '',
          errMsg: '',
          reqContent: [{
            name: '',
            required: 1,
            type: 'String',
            maxLength: 0,
            info: ''
          }],
          resContent: [{
            name: '',
            required: 1,
            type: 'String',
            maxLength: 0,
            info: ''
          }],
          show: 1
        }, row);
      },
      
      // 
      handleSubmit(){
        this.$refs['editForm'].validate(valid => {
          if (valid) {
            this.$confirm('', '', {})
              .then(() => {
                this.para.editLoading = true;
                let para = Object.assign({}, this.editData);
                commonApi.postData.call(this, '/common/editApi', para, data => {
                  this.para.editLoading = this.editVisible = false;
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.getLists();
                });
              });
          }
        });
      },
      
    },
    beforeMount () {
      
    },
    mounted(){
      this.getLists()
    }
  }
</script>
<style>
  .el-table .cell {
    white-space: nowrap;
  }
  
  .el-table__expanded-cell span {
    color: #99a9bf;
  }
</style>



