/* API */
import ajax from '../request'
export default {
  /**
   * 
   * @param {object}    data          
   * @param {string}    data.username 
   * @param {string}    data.password 
   * @param {function}  fn            
   * @param {Function}  errFn         
   */
  login(data, fn, errFn) {
    ajax.call(this, 'post', '/login', data, fn, true, errFn);
  },
  
  /* ************** () ************** */
  /**
   * /
   * @param  {String}   path  
   * @param  {object}   data  /
   * @param  {Function} fn    
   * */
  postData(path, data, fn){
    ajax.call(this, 'post', path, data, fn);
  },
  /**
   * 
   * @param  {String}   path  
   * @param  {object}   data  
   * @param  {Function} fn    
   * */
  deleteData(path, data, fn){
    ajax.call(this, 'get', path, data, fn);
  },
  /**
   * /
   * @param  {String}   path  
   * @param  {object}   data  
   * @param  {Function} fn    
   * */
  getData(path, data, fn){
    ajax.call(this, 'get', path, data, fn);
  },
  /* ************** () ************** */
}



