<style lang="less" scoped>
  .androidDown, .iosDown {
    position: relative;
  
  img {
    position: absolute;
    top: 50px;
    left: -55px;
    width: 150px;
    z-index: 5;
  }
  
  &
  :hover {
  
  img {
    display: block !important;
  }
    
  }
  }
</style>
<template>
  <!--  -->
  <header class="bg-dark dk header navbar navbar-fixed-top-xs">
    <!-- logo -->
    <div class="navbar-header aside-md">
      <a class="btn btn-link visible-xs" @click.stop="toggle_show_leftnav"
         :class="{'active':this.$store.state.nav.show_leftNav}">
        <i class="fa fa-bars"></i>
      </a>
      <a href='javascript:' class="navbar-brand" @click="screenfull">
        <img src="../../assets/images/logo.png" class="m-r-sm">
      </a>
      <a class="btn btn-link visible-xs" @click.stop="toggle_show_user"
         :class="{'active':this.$store.state.nav.show_user}">
        <i class="fa fa-cog"></i>
      </a>
    </div>
    <!--  -->
    <ul class="nav navbar-nav navbar-right m-n  hidden-xs nav-user"
        :class="{'open':this.$store.state.nav.show_user}">
      <!--  -->
      <li class="hidden-xs androidDown">
        <a href="javascript:">
          <i class="fa fa-android"></i>
        </a>
        <img src="../../assets/images/androidlogo.png" alt="" class="hide animated fadeInDown">
      </li>
      
      <!--  -->
      <li class="hidden-xs iosDown">
        <a href="javascript:">
          <i class="fa fa-apple"></i>
        </a>
        <img src="../../assets/images/IOSlogo.jpg" alt="" class="hide animated fadeInDown">
      </li>
      
      <!--  -->
      <li class="hidden-xs" :class="{'open':this.$store.state.nav.show_notification}">
        <a href="javascript:" class="dk" @click.stop="toggle_show_notification">
          <i class="fa fa-bell"></i>
          <span class="badge badge-sm up bg-danger m-l-n-sm"
                v-show="unreadNotice.length">{{unreadNotice.length}}</span>
        </a>
        <section class="dropdown-menu aside-xl">
          <section class="panel bg-white">
            <header class="panel-heading b-light bg-light">
              <strong v-if="unreadNotice.length"> <span class="count">{{unreadNotice.length}}</span> </strong>
              <strong v-else></strong>
            </header>
            <div class="list-group list-group-alt animated fadeInRight">
              <notification-item v-for="item in unreadNotice" :item="item" :key="item.id"></notification-item>
            </div>
            <footer class="panel-footer text-sm">
              <a href="javascript:" @click="toggle_show_all_notification"
                 :class="{'active':this.$store.state.nav.show_all_notification}"></a>
            </footer>
          </section>
        </section>
      </li>
      
      <!--  -->
      <li :class="{'open':this.$store.state.nav.show_usersetting}" class="dropdown">
        <a href="javascript:" @click="toggle_show_usersetting">
          <span class="thumb-sm avatar pull-left">
              <img :src="this.$store.state.user.userinfo.avatar">
          </span> {{this.$store.state.user.userinfo.username}}
          <b class="caret"></b>
        </a>
        <ul class="dropdown-menu animated fadeInRight">
          <span class="arrow top"></span>
          <li><a href="javascript:" @click="upPwdDialog = true;"></a></li>
          <li class="divider"></li>
          <li>
            <router-link to="/Documentation">CSS</router-link>
          </li>
          <li>
            <router-link to="/API">API</router-link>
          </li>
          <li class="visible-xs"><a href="javascript:" @click="logout"></a></li>
        </ul>
      </li>
      
      <!--  -->
      <li class="hidden-xs">
        <a href="javascript:" @click="logout" class="dropdown-toggle dker fa fa-sign-out"></a>
      </li>
    </ul>
    
    <!--  -->
    <el-dialog title="" v-model="upPwdDialog" size="tiny" @close="$refs.upPwdForm.resetFields()">
      <el-form :model="upPwdForm" :rules="upPwdRules" label-width="85px" ref="upPwdForm">
        <el-form-item label="" prop="oldPwd">
          <el-input v-model="upPwdForm.oldPwd" auto-complete="off" type="password"
                    @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPwd">
          <el-input v-model="upPwdForm.newPwd" auto-complete="off" type="password"
                    @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
        <el-form-item label="" prop="conPwd">
          <el-input v-model="upPwdForm.conPwd" auto-complete="off" type="password"
                    @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$refs.upPwdForm.resetFields()"></el-button>
        <el-button type="primary" @click="submitUpPwd('upPwdForm')"></el-button>
      </div>
    </el-dialog>
  </header>

</template>

<script>
  import screenfull from 'screenfull'
  import list from '../list'
  import {commonApi} from '@/config'
  export default{
    name: 'header-nav',
    data () {
      /**/
      let oldPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        else if (value.length < 6 || value.length > 16) {
          return callback(new Error(' 6  16 '))
        }
        callback()
      };
      let newPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        } else if (value.length < 6 || value.length > 16) {
          return callback(new Error(' 6  16 '))
        } else if (this.upPwdForm.conPwd !== '') {
          this.$refs.upPwdForm.validateField('conPwd');
        }
        callback();
      };
      let conPwd = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error(''));
        } else if (value.length < 6 || value.length > 16) {
          return callback(new Error(' 6  16 '))
        } else if (value !== this.upPwdForm.newPwd) {
          return callback(new Error('!'));
        }
        callback();
      };
      return {
        unreadNotice: [],
        upPwdDialog: false,
        upPwdForm: {
          oldPwd: '',
          newPwd: '',
          conPwd: ''
        },
        upPwdRules: {
          oldPwd: [
            {validator: oldPwd, trigger: 'blur'}
          ],
          newPwd: [
            {validator: newPwd, trigger: 'blur'}
          ],
          conPwd: [
            {validator: conPwd, trigger: 'blur'}
          ],
          
        }
      }
    },
    components: {
      'notification-item': list.NotificationItem
    },
    watch: {},
    methods: {
      /**
       * 
       */
      submitUpPwd(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let para = Object.assign({}, this.upPwdForm);
            commonApi.postData.call(this, '/common/upPwd', para, () => {
              this.$message({
                message: ',',
                type: 'success'
              });
              this.$store.dispatch('remove_userinfo').then(() => {
                this.upPwdDialog = false;
                this.$router.push('/signin');
              });
            })
          }
        })
      },
      /**
       * 
       */
      logout() {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('remove_userinfo').then(() => {
            this.$message({
              type: 'success',
              message: '',
              showClose: true
            });
            this.$router.push('/signin');
          }).catch(e => console.log(':' + e));
        }).catch(() => {
          this.$message({
            message: '',
            showClose: true
          });
        });
      },
      /**
       *  ()
       * */
      toggle_show_leftnav(){
        this.$store.dispatch('toggle_show_leftnav');
      },
      /**
       *  ()
       * */
      toggle_show_user(){
        this.$store.dispatch('toggle_show_user');
      },
      /**
       * 
       * */
      toggle_show_notification(){
        this.$store.dispatch('toggle_show_notification');
      },
      /**
       * 
       * */
      toggle_show_all_notification(){
        this.$store.dispatch('toggle_show_all_notification');
      },
      /**
       * 
       * */
      toggle_show_usersetting(e){
        this.$store.state.user.sys == 'pc' && e.stopPropagation();
        this.$store.dispatch('toggle_show_usersetting');
      },
      /**
       * /
       * */
      screenfull(){
        if (screenfull.enabled) {
          screenfull.toggle();
        }
      },
    },
    beforeMount () {
    },
  }
</script>



